a {
	display: block;
	text-decoration: none;
}
.card {
	width: 200px;
	height: fit-content;
	padding: 12px;
	border-radius: 12px;
	background-color: #3a8ac0;
	font-size: 14px;
	color: aliceblue;
	margin-left: 24px;
	box-shadow: 3px 3px 12px 1px black;
}
.card:first-child {
	margin-left: 0px;
}
.mainInfo {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.icon {
}
.title {
	font-size: 24px;
	font-weight: bold;
}
.description {
	margin-top: 4px;
	font-size: 16px;
}
.temperature {
	margin-top: 12px;
	font-size: 40px;
	font-weight: bold;
}
.temperature::after {
	content: "\2103";
}
.information > div {
	margin-top: 4px;
}
.closeBtn {
	display: flex;
	/* border: solid black 1px; */
	border-radius: 5px;
	justify-content: flex-end;
}
.singlCityWrap {
	display: flex;
	flex-direction: column;
	align-items: baseline;
}
.forecastList{
    margin-top: 24px;
    display: flex;
    color: red
}
